

























import { Product } from '../data/entities'
import { Component, Prop, Vue } from 'vue-property-decorator'

@Component
export default class ProductItem extends Vue {
  @Prop()
  private product!: Product;

  quantity: number = 1;

  handleAddToCart(){
    this.$emit('addToCart', {
      product: this.product,
      quantity: this.quantity,
    });
  }
};

